import React from "react";
import Modal from "./ViewComponents/ViewComponentsNav/Modal";
import ModalFull from "./ViewComponents/ViewComponentsNav/ModalFull";

import ViewCredito from "./ViewComponents/View_Credito";
import ViewCompartilhar from "./ViewComponents/View_Compartilhar";

import ViewInstrucao from "./ViewComponents/View_Instrucao";

import View from "./View";
import View2 from "./View2";
import View1 from "./View1";

// Componentes de roteamento
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import { Redirect } from 'react-router'
import logo from "./../Assets/logominigray.svg";
/**
 * @param {Null} Null Null
 * @description Classe geradora da função app
 * @constructor
 */



export default class Nav extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            redirect: false
        }
        //console.log(this.state.gap);

    }

  Tela_Home() {
    return (
      <div className="text-center">
        <h4 className="mb-5">
          Tem certeza que desaja volta a página inicial?
          <br /> Você perderá seu progresso no jogo.
        </h4>
        
          <button
            type="button"
            className="button-custom inicial bg-white arround-6"
            data-bs-dismiss="modal"
            onClick = { () => this.setState({redirect : true}) }
          >
            <span className="material-icons-outlined md-44 align-middle">
              home
            </span>
            INICIO
          </button>
      </div>
    );
  }
  render() {

  // ROTEAMENTO - colocar controle de estados da aplicação
  return (
    <div>
      <div className="d-flex nav-main bg-white1 align-items-center">
        <img className="logonav mx-3 my-md-4" src={logo} alt="logo EIC" />
        <i
          className=" flex material-icons md-grey md-36  mx-3 my-md-3"
          data-bs-toggle="modal"
          data-bs-target="#modal-home"
        >
          home
        </i>
        <i
          className=" flex material-icons md-grey md-36  mx-3 my-md-3"
          data-bs-toggle="modal"
          data-bs-target="#modal-shared"
        >
          share
        </i>
        <i
          className=" flex material-icons md-grey md-36  mx-3 my-md-3"
          data-bs-toggle="modal"
          data-bs-target="#modal-groups"
        >
          groups
        </i>
      </div>

      <Modal vinculo="modal-home" titulo="Ateção">
        {this.state.redirect ? <Redirect to="/" /> : this.Tela_Home() }
      </Modal>
      <ModalFull vinculo="modal-shared">
        <div className="view1">
          <ViewCompartilhar className="flex-fill" back={null} />
        </div>
      </ModalFull>
      <ModalFull vinculo="modal-groups">
        <div className="view">
          <ViewCredito className="flex-fill" back={null} />
        </div>
      </ModalFull>
      <ModalFull vinculo="modal-groups">
          <ViewCredito className="flex-fill" back={null} />
      </ModalFull>
    </div>
  );
  }
}
