
import React from "react";
import ReactDOM from "react-dom";


// Controladores

// Controladores
import ControleModo from './ControleModo'
import ConstrutorGame from './Construtor'
import Alert from "./../ViewComponentsJogo/SubViewComponentsJogo/Alert";

//Resource Boostraps Javascript


export default class ControleJogo extends React.Component {

    constructor(props) {
        super(props)
        console.log("Instancia");
        // fluxo inicial MODO
        this.ControleModo = new ControleModo()
        this.state = {
            focus: 0
        }

    }

    init(modo) {
        switch (modo) {
            case 1:

                this.ControleModo.curiosidades()

                break;
            case 2:
                this.ControleModo.usodasplantas()

                break;
            case 3:
                this.ControleModo.nomepopular()

                break;

            default:
                console.log("MODO INCORRETO!");
                break;
        }

        this.Construtor = new ConstrutorGame(this.ControleModo)
        let arr = Array()
        for (let index = 0; index < this.Construtor.tabuleiro.palavras.length; index++) {
            arr.push(Array(this.Construtor.tabuleiro.palavras[index].length).fill(null));

        }

        // Variaveis Dinamicas
        this.state = {
            focus: 0,
            statusBoard: Array(this.ControleModo.getAltura() * this.ControleModo.getLargura()).fill(null),
            tips: this.ControleModo.tips,
            hitWord: Array(this.ControleModo.getQuantidadePalavras()).fill(false),
            statusWords: arr,
            Tips: Array(this.ControleModo.getQuantidadePalavras()).fill(false),

        }


        console.log(this.Construtor);
    }

    // Manage of words

    next() {
        this.state.focus = this.state.focus === this.Construtor.principal.length - 1 ? 0 : this.state.focus + 1;

        //console.log(this.state)
    }

    prev() {
        this.state.focus = this.state.focus === 0 ? this.Construtor.principal.length - 1 : this.state.focus - 1;

        //console.log(this.state)
    }

    selectFocus(index) {
        console.log(index);
        this.state.focus = index;
    }

    // Manage of tips

    useTip() {
        if(!this.state.Tips[this.state.focus]){
            if (this.state.tips)
                this.state.Tips[this.state.focus] = true
            this.state.tips = this.state.tips === 0 ? 0 : this.state.tips - 1;
            
        }
        
        //console.log(this.state);
    }


    //Manage of Keyboard letter

    deleteLetter() {
        //
    }

    selectLetter(vinculo, posi, letter) {
        
        if( this.state.statusBoard[vinculo] !== this.Construtor.tabuleiro.gabarito[vinculo]){
            this.state.statusBoard[vinculo] = letter

            
            if(this.Construtor.tabuleiro.vinculo[vinculo].length > 1){
                let palavra1 = this.Construtor.tabuleiro.vinculo[vinculo][0]
                let palavra2 = this.Construtor.tabuleiro.vinculo[vinculo][1]
                let posi1 = this.Construtor.tabuleiro.posicoes[palavra1].indexOf(vinculo)
                let posi2 = this.Construtor.tabuleiro.posicoes[palavra2].indexOf(vinculo)
                this.state.statusWords[palavra1][posi1] = letter
                this.state.statusWords[palavra2][posi2] = letter
            }
            else
            this.state.statusWords[this.state.focus][posi] = letter
            
            
        }
        if(!this.state.hitWord[this.state.focus]){
            this.hitWord()
        }
        
    }



    // Game Rules

    //Words

    hitWord() {
        //console.log(this.state.statusWords[this.state.focus]);
        //console.log(this.Construtor.principal[this.state.focus].letras);
        let teste = true;
        for (let index = 0; index < this.state.statusWords[this.state.focus].length; index++) {
            const status = this.state.statusWords[this.state.focus][index];
            const gabarito = this.Construtor.principal[this.state.focus].letras[index];
            //console.log(status);
            if (status !== gabarito) {
                teste = false
                break
            }
        }

        console.log(this.Construtor.principal);
        

        this.state.hitWord[this.state.focus] = teste
        if(teste) this.renderAlert(this.Construtor.principal[this.state.focus].Resposta);
    }

    renderAlert(palavra) {
        const element = <Alert palavra={palavra}/>;
        console.log(element);
        
        ReactDOM.render(
          element,
          document.getElementById("notificacao"),
          function () {
              
            document.getElementById("myAlert").classList.add("d-none")
            document.getElementById("myAlert").classList.remove("d-none")
            document.getElementById("myAlert").classList.replace("close", "show")
            document.getElementById("closeAlert").addEventListener("click", function () {
                    document.getElementById("myAlert").classList.replace("show", "close")
                }, false);
            setTimeout(() =>{
                document.getElementById("myAlert").classList.replace("show", "close")
            
            }, 3000);
            setTimeout(() =>{
                document.getElementById("myAlert").classList.add("d-none")
            
            }, 4500);
          }
        );
      }




}